/*
侧滑菜单
*/
.mw-sub-nav {
  display: none;
}
.mw-sub-nav-on {
  display: block;
  overflow: hidden;
  position: fixed;
  max-width: 750px;
  width: 100%;
  height: 100%;
  left: 50%;
  transform: translateX(-50%);
  -webkit-transform: translateX(-50%);
  top: 0;
  z-index: 98;
}
.mw-sub-nav-on .mw-nav-mask {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 98;
  left: 0;
  top: 0;
  background: rgba(0, 0, 0, 0.5);
  animation: 0.5s fadeInmask ease-out;
  -webkit-animation: 0.5s fadeInmask ease-out;
}
.mw-sub-nav-on .mw-nav-mask span {
  position: absolute;
  left: 1rem;
  width: 0.4rem;
  height: 0.4rem;
  background: #ffff;
  top: 50%;
  text-align: center;
  font-size: 50px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  color: #fff;
  opacity: 1;
  background: url(../../static/img/basic_icon.2e158d2.png) no-repeat 0 -5.8rem;
  background-size: 1.5rem;
  z-index: 98;
}
.mw-sub-nav-on .mw-nav-con {
  position: absolute;
  width: 75%;
  height: 100%;
  background: #FFFFFF;
  left: 25%;
  top: 0;
  z-index: 98;
  animation: 0.3s fadeInRight ease-out;
  -webkit-animation: 0.3s fadeInRight ease-out;
}
.mw-nav-fot a span {
  color: #fff;
}
@-webkit-keyframes fadeInmask {
0% {
    opacity: 0;
}
40% {
    opacity: 0;
}
to {
    opacity: 1;
}
}
@keyframes fadeInmask {
0% {
    opacity: 0;
}
40% {
    opacity: 0;
}
to {
    opacity: 1;
}
}
@-webkit-keyframes fadeInRight {
0% {
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
}
to {
    -webkit-transform: none;
            transform: none;
}
}
@keyframes fadeInRight {
0% {
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
}
to {
    -webkit-transform: none;
            transform: none;
}
}
.mw-nav-fot a span {
  color: #fff;
}
